/* OUTER PAGE WRAPPER ‚Äì full width */
.placement-page {
  width: 100%;
  padding: 50px 0 70px;
  font-family: "Poppins", sans-serif;
}

/* INNER CONTAINER ‚Äì same as other pages */
.placement-inner {
  width: 90%;
  max-width: 1250px;
  margin: 0 auto; /* üëà pure center */
}

/* ================= HERO ================= */
.placement-hero {
  text-align: center;
  padding: 10px 0 40px;
}

.placement-hero h1 {
  font-size: 40px;
  font-weight: 800;
  line-height: 1.3;
  margin: 0;
}

.placement-hero span {
  color: #ff7a00;
}

.placement-hero p {
  margin-top: 12px;
  color: #555;
  font-size: 16px;
}

/* ================= STATS ================= */
.stats-section {
  margin-top: 20px;
  margin-bottom: 60px;
}

/* üëâ Stats grid block khud center me */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 28px;
  max-width: 1150px;     /* grid ki apni width */
  margin: 0 auto;        /* üëà pure center horizontally */
}

.stat-card {
  background: #fff;
  border-radius: 22px;
  padding: 38px 28px;
  border: 1px solid #f0f0f0;
  box-shadow: 0 8px 28px rgba(0, 0, 0, 0.08);

  /* vertical stack + equal gaps */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  text-align: center;

  transition: 0.25s ease;
}

.stat-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 14px 34px rgba(0, 0, 0, 0.12);
  border-color: #ffd3ad;
}

.stat-icon {
  font-size: 32px;
}

.stat-card h2 {
  font-size: 32px;
  font-weight: 800;
  color: #ff7a00;
  margin: 0;
}

.stat-sub {
  color: #666;
  font-size: 15px;
  line-height: 1.45;
  text-align: center;
  width: 100%;
  margin: 0;
  display: block;
}

/* ================= ACHIEVERS ================= */
.achievers-section h2 {
  font-size: 30px;
  font-weight: 800;
  margin-bottom: 30px;
  text-align: center;
}

/* üëâ Achievers grid bhi center me */
.achievers-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 26px;
  max-width: 1150px;
  margin: 0 auto;
}

.achiever-card {
  background: white;
  padding: 18px 18px 22px;
  border-radius: 18px;
  text-align: center;
  border: 1px solid #f2f2f2;
  transition: 0.25s ease;
}

.achiever-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 14px 32px rgba(0, 0, 0, 0.14);
  border-color: #ffddb9;
}

.achiever-img {
  width: 100%;
  height: 210px;
  object-fit: cover;
  border-radius: 14px;
  margin-bottom: 14px;
}

.achiever-name {
  font-size: 16px;
  font-weight: 700;
}

.exam {
  color: #ff7a00;
  font-weight: 600;
  font-size: 14px;
  margin-top: 4px;
}

.year {
  margin-top: 3px;
  color: #777;
  font-size: 13px;
}

/* ================= CTA SECTION ================= */
.cta-section {
  text-align: center;
  margin-top: 80px;
  padding: 45px 30px;
  background: linear-gradient(135deg, #fff2e3, #ffe8d0);
  border-radius: 22px;
  box-shadow: 0 12px 30px rgba(255, 122, 0, 0.2);
}

.cta-section h2 {
  font-size: 28px;
  margin-bottom: 8px;
}

.cta-section p {
  color: #555;
  margin-bottom: 18px;
  font-size: 15px;
}

.join-btn {
  padding: 12px 30px;
  background: #ff7a00;
  color: white;
  font-size: 15px;
  font-weight: 700;
  border: none;
  border-radius: 32px;
  cursor: pointer;
  box-shadow: 0 10px 24px rgba(255, 122, 0, 0.5);
  transition: 0.25s ease;
}

.join-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 14px 34px rgba(255, 122, 0, 0.6);
}

/* ================= RESPONSIVE ================= */
@media (max-width: 1024px) {
  .stats-grid,
  .achievers-grid {
    grid-template-columns: repeat(2, 1fr);
    max-width: 700px;
  }

  .placement-hero h1 {
    font-size: 32px;
  }
}

@media (max-width: 600px) {
  .placement-inner {
    width: 92%;
  }

  .stats-grid,
  .achievers-grid {
    grid-template-columns: 1fr;
    max-width: 400px;
  }

  .placement-hero h1 {
    font-size: 28px;
  }
}
