.popup-overlay {
  position: fixed;
  top: 0; left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.55);
  backdrop-filter: blur(4px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 3000;
}

.popup-box {
  width: 420px;
  padding: 30px;
  background: white;
  border-radius: 20px;
  text-align: center;
}

.popup-btn {
  margin-top: 20px;
  padding: 10px 24px;
  background: #ff7a00;
  color: white;
  border: none;
  border-radius: 25px;
  cursor: pointer;
}

.zoom-in {
  animation: zoom-in 0.4s ease;
}

@keyframes zoom-in {
  from { transform: scale(0.7); opacity: 0; }
  to   { transform: scale(1); opacity: 1; }
}
