.contact-page {
  width: 90%;
  max-width: 1250px;
  margin: 0 auto;
  padding: 40px 0;
  font-family: sans-serif;
}

/* HERO */
.contact-hero {
  text-align: center;
  padding: 20px 0 40px;
}

.contact-hero h1 {
  font-size: 42px;
  font-weight: 800;
}

.contact-hero p {
  font-size: 17px;
  margin-top: 10px;
  color: #555;
}

/* CONTACT GRID */
.contact-grid {
  display: grid;
  grid-template-columns: 1fr 1.2fr;
  gap: 40px;
  margin-top: 30px;
}

/* LEFT INFO */
.contact-info {
  padding: 25px 30px;
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 6px 28px rgba(0,0,0,0.08);
}

.contact-info h2 {
  margin-bottom: 20px;
  font-size: 26px;
}

.info-box {
  display: flex;
  align-items: center;
  gap: 15px;
  background: #f7f7f7;
  border-radius: 12px;
  padding: 12px 15px;
  margin-bottom: 15px;
}

.info-icon {
  font-size: 20px;
  color: #ff7a00;
}

/* RIGHT FORM */
.contact-form {
  padding: 25px 30px;
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 6px 28px rgba(0,0,0,0.08);
}

.contact-form h2 {
  margin-bottom: 20px;
  font-size: 26px;
}

.contact-form form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 12px 15px;
  border-radius: 12px;
  border: 1px solid #ddd;
  font-size: 15px;
  outline: none;
}

.contact-form textarea {
  resize: none;
}

.contact-form button {
  width: fit-content;
  padding: 12px 28px;
  background: #ff7a00;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  font-weight: 600;
}

.contact-form button:hover {
  background: #e56d00;
}

/* LOCATIONS */
.location-title {
  margin: 50px 0 20px;
  font-size: 28px;
  font-weight: 700;
}

.location-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 25px;
}

.location-card {
  background: white;
  padding: 20px 25px;
  border-radius: 14px;
  box-shadow: 0 6px 28px rgba(0,0,0,0.08);
  text-align: center;
}

.location-card h3 {
  font-size: 20px;
  margin-bottom: 8px;
}

/* RESPONSIVE */
@media (max-width: 900px) {
  .contact-grid {
    grid-template-columns: 1fr;
  }
  .location-grid {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 600px) {
  .location-grid {
    grid-template-columns: 1fr;
  }
}
