/* ===========================
   GLOBAL WRAPPER
=========================== */
.test-wrapper {
  font-family: "Poppins", sans-serif;
  background: #f4f6fb;
  padding: 22px;
  min-height: 100vh;
}

/* ===========================
   PREMIUM BACK BUTTON
=========================== */
.back-btn {
  margin: 0 auto 14px;
  max-width: 1200px;
  display: block;
  background: #ffffff;
  border-radius: 12px;
  padding: 8px 14px;
  font-size: 13px;
  font-weight: 600;
  border: 1.5px solid #e3e5ea;
  color: #374151;
  cursor: pointer;
  transition: 0.22s ease;
  width: fit-content;
}

.back-btn:hover {
  background: #f3f4f7;
  border-color: #cfd3d8;
  box-shadow: 0px 3px 8px rgba(0,0,0,0.05);
}

/* ===========================
   TOP BAR (HEADER)
=========================== */
.test-topbar {
  max-width: 1200px;
  margin: 0 auto 18px;
  background: #ffffff;
  padding: 18px 24px;
  border-radius: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 3px 12px rgba(0,0,0,0.05);
}

.test-title {
  font-size: 23px;
  font-weight: 700;
  color: #111827;
  letter-spacing: -0.4px;
}

.test-subtitle {
  font-size: 12px;
  color: #6b7280;
  margin-top: 2px;
}

.test-top-right {
  display: flex;
  align-items: center;
  gap: 18px;
}

.candidate-info {
  text-align: right;
}

.candidate-label {
  font-size: 11px;
  color: #9ca3af;
  line-height: 16px;
}

.candidate-name {
  font-size: 13px;
  font-weight: 600;
  color: #111827;
}

.timer-box {
  background: #111827;
  color: #ffffff;
  padding: 8px 14px;
  border-radius: 10px;
  font-size: 12px;
  min-width: 110px;
  text-align: center;
  letter-spacing: 0.2px;
}

/* ===========================
   SUBJECT TABS
=========================== */
.subject-tabs {
  max-width: 1200px;
  margin: 0 auto 16px;
  display: flex;
  gap: 12px;
}

.subject-tab {
  border: none;
  background: #e5e7eb;
  padding: 8px 20px;
  border-radius: 999px;
  font-size: 13px;
  cursor: pointer;
  transition: 0.22s;
  font-weight: 500;
}

.subject-tab:hover {
  background: #d4d7dd;
}

.subject-tab.active {
  background: #2563eb;
  color: #ffffff;
  box-shadow: 0px 3px 8px rgba(37,99,235,0.35);
}

/* ===========================
   MAIN GRID AREA
=========================== */
.test-main {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 2.1fr 0.9fr;
  gap: 20px;
}

/* ===========================
   QUESTION CARD
=========================== */
.question-area {
  background: #ffffff;
  padding: 24px 26px;
  border-radius: 16px;
  box-shadow: 0px 3px 12px rgba(0,0,0,0.04);
  animation: fadeUp 0.25s ease;
}

@keyframes fadeUp {
  from { opacity: 0; transform: translateY(8px); }
  to { opacity: 1; transform: translateY(0); }
}

.question-header {
  display: flex;
  justify-content: space-between;
  padding-bottom: 10px;
  border-bottom: 1px solid #e6e6e6;
  font-size: 14px;
  font-weight: 600;
}

.question-header .marks-label {
  color: #b91c1c;
  font-weight: 600;
}

.question-text {
  margin-top: 18px;
  margin-bottom: 18px;
  font-size: 15px;
  line-height: 1.6;
  color: #111827;
  font-weight: 500;
}

/* ===========================
   SLIM CBT OPTIONS
=========================== */
.options-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.option-item {
  padding: 11px 14px;
  display: flex;
  align-items: center;
  gap: 12px;
  border-radius: 9px;
  border: 1.5px solid transparent;
  background: #f9fafc;
  cursor: pointer;
  transition: 0.25s;
}

.option-item input {
  transform: scale(1.22);
  accent-color: #2563eb;
  cursor: pointer;
}

.option-item:hover {
  background: #f0f5ff;
  box-shadow: 0 2px 6px rgba(37, 99, 235, 0.15);
}

.option-item.selected {
  border-color: #2563eb;
  background: #e0e9ff;
  box-shadow: 0 2px 6px rgba(37, 99, 235, 0.25);
}

.option-letter {
  font-weight: 700;
  min-width: 22px;
  color: #111827;
}

/* ===========================
   ACTION BUTTONS
=========================== */
.question-actions {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  margin-top: 18px;
}

.btn {
  padding: 10px 18px;
  border-radius: 999px;
  cursor: pointer;
  font-size: 12px;
  font-weight: 600;
  transition: 0.22s ease;
  border: none;
}

.btn.primary {
  background: #16a34a;
  color: white;
}
.btn.primary:hover { background: #128033; }

.btn.secondary {
  background: #fff;
  border: 1.4px solid #d3d3d3;
}
.btn.secondary:hover { background: #f1f1f1; }

.btn.warning {
  background: #f97316;
  color: white;
}
.btn.warning:hover { background: #e65c08; }

.btn.info {
  background: #0ea5e9;
  color: white;
}
.btn.info:hover { background: #067caf; }

.btn.ghost {
  background: #fff;
  border: 1.4px solid #cfcfcf;
}
.btn.ghost:hover { background: #f7f7f7; }

/* ===========================
   SIDE PALETTE
=========================== */
.test-sidebar {
  background: #ffffff;
  border-radius: 16px;
  padding: 18px;
  box-shadow: 0px 3px 12px rgba(0,0,0,0.05);
  animation: fadeUp 0.3s ease;
}

.legend {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 6px;
  font-size: 12px;
  margin-bottom: 10px;
}

.legend-item {
  display: flex;
  align-items: center;
  gap: 6px;
}

.legend-dot {
  width: 14px;
  height: 14px;
  border-radius: 50%;
}

.legend-dot.answered { background: #16a34a; }
.legend-dot.not-answered { background: #ef4444; }
.legend-dot.not-visited { background: #9ca3af; }
.legend-dot.marked { background: #3b82f6; }

.palette-title {
  font-weight: 600;
  margin: 12px 0 8px;
  font-size: 13px;
}

.palette-scroll {
  max-height: 250px;
  overflow-y: auto;
  padding-right: 5px;
}

.palette-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.palette-btn {
  border: none;
  padding: 7px 12px;
  border-radius: 12px;
  font-size: 12px;
  cursor: pointer;
  transition: 0.25s;
  font-weight: 500;
}

.palette-btn.answered { background: #16a34a; color: white; }
.palette-btn.not-answered { background: #ef4444; color: white; }
.palette-btn.not-visited { background: #9ca3af; color: white; }
.palette-btn.marked { background: #3b82f6; color: white; }

.palette-btn.current {
  transform: scale(1.08);
  box-shadow: 0 0 0 3px #2563eb42;
}

/* ===========================
   SUBMIT BUTTON
=========================== */
.submit-btn {
  width: 100%;
  background: #f97316;
  color: white;
  border-radius: 999px;
  padding: 12px;
  margin-top: 20px;
  font-weight: 600;
  font-size: 13px;
  transition: 0.25s;
}
.submit-btn:hover { background: #e65c08; }

/* ===========================
   RESPONSIVE FIXES
=========================== */
@media (max-width: 920px) {
  .test-main {
    grid-template-columns: 1fr;
  }
}
