.preview-page {
  padding: 40px;
}

.preview-page h2 {
  font-size: 28px;
  font-weight: 800;
}

.preview-box {
  margin-top: 20px;
  padding: 40px;
  background: white;
  border-radius: 15px;
  box-shadow: 0 5px 20px rgba(0,0,0,0.08);
  text-align: center;
  font-size: 18px;
  color: #444;
}

.submit-btn {
  padding: 12px 30px;
  background: #ff7a00;
  border: none;
  color: white;
  border-radius: 30px;
  cursor: pointer;
  font-weight: 600;
  margin-top: 20px;
}

.success-msg {
  margin-top: 25px;
  padding: 12px;
  color: green;
  background: #d9ffd9;
  width: fit-content;
  border-radius: 8px;
  font-weight: 600;
}

/* COMMENTS SECTION */
.comments-section {
  margin-top: 50px;
  text-align: left;
  border-top: 1px solid #eee;
  padding-top: 30px;
}

.comments-section h3 {
  font-size: 22px;
  margin-bottom: 20px;
  color: #333;
}

.comments-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 25px;
}

.comment-item {
  background: #f9f9f9;
  padding: 15px;
  border-radius: 10px;
  border-left: 4px solid #ff7a00;
}

.comment-item strong {
  display: block;
  font-size: 14px;
  color: #555;
  margin-bottom: 5px;
}

.comment-item p {
  margin: 0;
  font-size: 15px;
  color: #222;
}

.comment-input-box {
  display: flex;
  gap: 10px;
}

.comment-input-box textarea {
  flex: 1;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 8px;
  resize: none;
  height: 50px;
  font-family: inherit;
}

.comment-input-box button {
  background: #333;
  color: white;
  border: none;
  padding: 0 25px;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  transition: 0.3s;
}

.comment-input-box button:hover {
  background: #ff7a00;
}
