.faculty-page {
  width: 90%;
  max-width: 1250px;
  margin: 0 auto;
  padding: 40px 0;
}

/* HEADER */
.faculty-header {
  padding: 40px 0;
}

.faculty-title {
  font-size: 36px;
  font-weight: 800;
}

.faculty-title span {
  color: #ff7a00;
}

.faculty-subtitle {
  margin-top: 10px;
  font-size: 17px;
  color: #555;
  max-width: 600px;
}

/* FACULTY GRID */
.faculty-grid {
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 35px;
}

/* CARD */
.faculty-card {
  background: #ffffff;
  border-radius: 15px;
  padding: 20px;
  text-align: center;
  border: 1px solid #eee;
  transition: 0.3s ease;
}

.faculty-card:hover {
  box-shadow: 0 10px 30px rgba(0,0,0,0.08);
  transform: translateY(-5px);
}

/* IMAGE */
.faculty-img {
  width: 100%;
  height: 200px;
  border-radius: 12px;
  object-fit: cover;
  margin-bottom: 15px;
}

/* TEXT */
.faculty-name {
  font-size: 20px;
  font-weight: 700;
}

.faculty-role {
  color: #ff7a00;
  font-weight: 600;
  margin-top: 5px;
}

.faculty-exp {
  margin-top: 5px;
  color: #666;
  font-size: 14px;
}

/* MOBILE FIX */
@media(max-width: 900px){
  .faculty-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media(max-width: 600px){
  .faculty-grid {
    grid-template-columns: 1fr;
  }
}
