.notification-sidebar-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.4);
  z-index: 9998;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease;
}

.notification-sidebar-overlay.open {
  opacity: 1;
  visibility: visible;
}

.notification-sidebar {
  position: fixed;
  top: 0;
  right: -350px;
  /* Start off-screen */
  width: 350px;
  height: 100vh;
  background: #ffffff;
  box-shadow: -2px 0 10px rgba(0, 0, 0, 0.1);
  z-index: 9999;
  transition: right 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  display: flex;
  flex-direction: column;
}

.notification-sidebar.open {
  right: 0;
}

/* Header */
.notification-sidebar-header {
  padding: 20px;
  border-bottom: 1px solid #f0f0f0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #fff;
  width: 100%;
}

.notification-sidebar-header h3 {
  margin: 0;
  font-size: 1.2rem;
  color: #333;
  flex: 1;
}

.close-btn-sidebar {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #666;
  padding: 0;
  line-height: 1;
  transition: color 0.2s;
}

.close-btn-sidebar:hover {
  color: #000;
}

/* Content */
.notification-sidebar-content {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
}

.notification-item {
  padding: 15px;
  border-radius: 8px;
  background: #f9f9f9;
  margin-bottom: 15px;
  border-left: 4px solid #007bff;
  /* Primary color accent */
  transition: transform 0.2s, background 0.2s;
}

.notification-item:hover {
  transform: translateX(-2px);
  background: #f0f4ff;
}

.notification-item h4 {
  margin: 0 0 5px 0;
  font-size: 0.95rem;
  color: #333;
}

.notification-item p {
  margin: 0;
  font-size: 0.85rem;
  color: #666;
  line-height: 1.4;
}

.notification-time {
  display: block;
  margin-top: 8px;
  font-size: 0.75rem;
  color: #999;
}

.no-notifications {
  text-align: center;
  color: #999;
  margin-top: 50px;
}

/* Mobile Responsive */
@media (max-width: 480px) {
  .notification-sidebar {
    width: 300px;
    right: -300px;
  }
}

.clear-all-btn {
  background: #ff6c00;
  border: none;
  color: #fff;
  cursor: pointer;
  font-size: 0.9rem;
  padding: 8px 16px;
  border-radius: 4px;
  transition: background 0.2s;
  text-decoration: none;
  flex: 1;
}

.clear-all-btn:hover {
  background: #e65100;
}

.notification-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.delete-notification-btn {
  background: none;
  border: none;
  font-size: 1.2rem;
  line-height: 1;
  color: #999;
  cursor: pointer;
  padding: 0 0 0 10px;
}

.delete-notification-btn:hover {
  color: #ff0000;
}

.notification-sidebar-footer {
  padding: 20px;
  border-top: 1px solid #f0f0f0;
  background: #fff;
}