.admission-page {
  width: 100%;
  padding: 40px;
  background: linear-gradient(135deg, #fafafa, #f3f3f3);
  font-family: 'Poppins', sans-serif;
}

/* ================= HERO SECTION ================= */
.hero-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  border-radius: 25px;
  padding: 60px;
  box-shadow: 0 15px 40px rgba(0,0,0,0.1);
  margin-bottom: 70px;
  animation: fadeSlide 0.8s ease;
  gap: 40px;
}

/* LEFT CONTENT */
.hero-left {
  width: 55%;
}

.hero-left h1 {
  font-size: 48px;
  font-weight: 800;
  line-height: 1.2;
  max-width: 520px;
  color: #111;
}

.hero-left span {
  color: #111;
}

.hero-sub {
  margin-top: 10px;
  color: #555;
  font-size: 17px;
}

/* ====== RATING ROW (clean + less gap) ====== */
.hero-rating {
  margin-top: 8px;
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 13px;
  color: #555;
}

.hero-rating-stars {
  font-size: 16px;
  line-height: 1;
  letter-spacing: 2px;
}

.hero-rating-score {
  font-weight: 600;
}

.hero-rating-dot {
  font-size: 16px;
  margin: 0 2px;
}

/* BUTTONS */
.hero-buttons {
  margin-top: 18px;
  display: flex;
  gap: 15px;
}

.btn-primary {
  background: #ff7a00;
  padding: 12px 30px;
  color: white;
  border: none;
  border-radius: 40px;
  cursor: pointer;
  font-size: 15px;
  font-weight: 600;
  transition: 0.3s;
}

.btn-outline {
  padding: 12px 30px;
  border-radius: 40px;
  border: 2px solid #ff7a00;
  background: white;
  color: #ff7a00;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s;
}

.btn-primary:hover,
.btn-outline:hover {
  transform: translateY(-4px);
}

.glow {
  box-shadow: 0 0 15px rgba(255, 122, 0, 0.5);
}

/* ================= FORM RIGHT SIDE ================= */
.hero-form {
  width: 35%;
  background: #ffffff;
  padding: 35px;
  border-radius: 20px;
  box-shadow: 0 8px 28px rgba(0,0,0,0.12);
  animation: pop 0.6s ease;
}

.glass {
  background: rgba(255,255,255,0.6);
  backdrop-filter: blur(12px);
}

.hero-form h3 {
  margin-bottom: 22px;
  font-size: 24px;
  font-weight: 600;
}

.hero-form input,
.hero-form select {
  width: 100%;
  margin-bottom: 15px;
  padding: 13px;
  font-size: 15px;
  border-radius: 12px;
  border: 1px solid #ddd;
}

.full {
  width: 100%;
}

/* ================= PROGRAM SECTION ================= */
.program-section {
  margin-top: 70px;
}

.program-section h2 {
  font-size: 34px;
  margin-bottom: 40px;
}

.program-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 35px;
}

.program-card {
  background: white;
  padding: 35px;
  border-radius: 20px;
  box-shadow: 0 6px 25px rgba(0,0,0,0.08);
  transition: 0.3s;
}

.program-card:hover {
  transform: translateY(-8px);
}

.btn-program {
  margin-top: 18px;
  padding: 10px 26px;
  background: #ff7a00;
  border: none;
  border-radius: 30px;
  color: white;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s;
}

.btn-program:hover {
  transform: translateY(-4px);
}

/* ============ RESPONSIVE FIXES ============ */
@media (max-width: 1024px) {
  .hero-section {
    padding: 40px;
    flex-direction: column;
    text-align: center;
  }

  .hero-left {
    width: 100%;
  }

  .hero-left h1 {
    font-size: 38px;
    margin: 0 auto;
  }

  .hero-rating {
    justify-content: center;
    flex-wrap: wrap;
  }

  .hero-form {
    width: 100%;
    margin-top: 30px;
  }

  .program-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .hero-left h1 {
    font-size: 34px;
  }

  .program-grid {
    grid-template-columns: 1fr;
  }
}

@keyframes fadeSlide {
  from { opacity: 0; transform: translateY(20px); }
  to   { opacity: 1; transform: translateY(0); }
}

@keyframes pop {
  from { opacity: 0; transform: scale(0.95); }
  to   { opacity: 1; transform: scale(1); }
}
