.footer {
  background: #111;
  color: white;
  padding: 50px 0 20px;
  margin-top: 60px;
}

.footer-container {
  width: 90%;
  max-width: 1250px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 40px;
}

/* LOGO */
.footer-logo {
  font-size: 26px;
  font-weight: 800;
  color: #ff7a00;
}

.footer-col p {
  color: #ccc;
  margin-top: 10px;
  line-height: 22px;
}

/* SOCIAL ICONS */
.footer-social {
  display: flex;
  gap: 15px;
  margin-top: 15px;
}

.footer-social svg {
  font-size: 22px;
  cursor: pointer;
  transition: 0.3s;
}

.footer-social svg:hover {
  color: #ff7a00;
}

/* LINKS */
.footer-col a {
  display: block;
  color: #ccc;
  margin: 6px 0;
  text-decoration: none;
  transition: 0.3s;
}

.footer-col a:hover {
  color: #ff7a00;
}

/* COL HEADINGS */
.footer-col h3 {
  margin-bottom: 15px;
  font-size: 20px;
}

/* BOTTOM */
.footer-bottom {
  text-align: center;
  margin-top: 40px;
  padding-top: 15px;
  border-top: 1px solid #333;
  color: #aaa;
}

/* RESPONSIVE */
@media (max-width: 900px) {
  .footer-container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 600px) {
  .footer-container {
    grid-template-columns: 1fr;
  }
}
