/* HERO MAIN */
.hero {
  width: 100%;
  background: linear-gradient(135deg, #ff7a00, #ff9b3b);
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
  min-height: 540px;
}

/* CONTAINER */
.hero-container {
  width: 90%;
  max-width: 1400px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  z-index: 3;
  padding: 60px 0;
}

/* LEFT SIDE */
.hero-left {
  width: 48%;
  color: #fff;
}

.hero-card {
  padding: 30px 0;
}

.hero-left h1 {
  font-size: 48px;
  font-weight: 800;
  line-height: 1.15;
  margin-bottom: 20px;
}

.hero-left h1 span {
  background: #fff;
  color: #ff7a00;
  padding: 4px 12px;
  border-radius: 8px;
  display: inline-block;
  margin-top: 8px;
}

.hero-left p {
  font-size: 18px;
  line-height: 1.6;
  margin: 20px 0 30px;
  opacity: 0.95;
  max-width: 90%;
}

/* BUTTONS */
.hero-buttons {
  display: flex;
  gap: 16px;
  margin-top: 30px;
}

.btn {
  padding: 14px 32px;
  border-radius: 30px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
  outline: none;
}

.primary {
  background: #fff;
  color: #ff7a00;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
}

.outline {
  background: transparent;
  border: 2px solid #fff;
  color: #fff;
}

.outline:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-3px);
}

/* RIGHT SIDE */
.hero-right {
  width: 50%;
  position: relative;
  z-index: 5;
}

.hero-img {
  width: 100%;
  height: 400px;
  object-fit: cover;
  border-radius: 20px;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease;
}

.hero-img:hover {
  transform: scale(1.02);
}

/* Student Count Badge */
.student-count {
  position: absolute;
  bottom: -15px;
  right: 20px;
  background: rgba(255, 255, 255, 0.95);
  padding: 15px 25px;
  border-radius: 15px;
  text-align: center;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  z-index: 10;
}

.count-number {
  font-size: 32px;
  font-weight: 800;
  color: #ff7a00;
  line-height: 1;
}

.count-text {
  font-size: 14px;
  color: #333;
  font-weight: 600;
  margin-top: 5px;
}

/* WAVE */
.hero-wave {
  position: absolute;
  left: 0;
  bottom: -1px;
  width: 100%;
  height: 100px;
  background: #fff;
  clip-path: path("M0,64L48,53.3C96,43,192,21,288,26.7C384,32,480,64,576,69.3C672,75,768,53,864,58.7C960,64,1056,96,1152,106.7C1248,117,1344,107,1392,101.3L1440,96L1440,160L0,160Z");
}

/* RESPONSIVE */
@media (max-width: 900px) {
  .hero-container {
    flex-direction: column-reverse;
    text-align: center;
    padding: 40px 0;
  }

  .hero-left,
  .hero-right {
    width: 100%;
  }

  .hero-left h1 {
    font-size: 36px;
  }

  .hero-left p {
    max-width: 100%;
    font-size: 16px;
  }

  .hero-img {
    height: 280px;
    margin-top: 30px;
  }

  .student-count {
    bottom: -10px;
    right: 50%;
    transform: translateX(50%);
  }

  .hero-buttons {
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .hero-left h1 {
    font-size: 30px;
  }

  .hero-buttons {
    flex-direction: column;
    gap: 12px;
  }

  .btn {
    width: 100%;
    max-width: 280px;
    margin: 0 auto;
  }
}