/* ================================
   GLOBAL THEME + DARK MODE
===================================*/
:root {
  --bg: #f4f6fb;
  --bg-soft: #e5ecff;
  --card: #ffffff;
  --card-soft: #f9fafb;
  --text: #111827;
  --text-muted: #6b7280;

  --primary: #2563eb;
  --primary-soft: rgba(37, 99, 235, 0.08);
  --primary-dark: #1d4ed8;

  --border: #d1d5db;
  --border-soft: #e5e7eb;

  --success: #16a34a;
  --danger: #ef4444;
  --warning: #f59e0b;
  --info: #0ea5e9;
  --not-visited: #9ca3af;

  --radius: 10px;
  --radius-lg: 14px;

  --shadow-soft: 0 4px 15px rgba(15, 23, 42, 0.08);
  --shadow-card: 0 6px 24px rgba(15, 23, 42, 0.12);
}

html[data-theme="dark"] {
  --bg: #020617;
  --bg-soft: #0b1220;
  --card: #020617;
  --card-soft: #020617;
  --text: #e5e7eb;
  --text-muted: #9ca3af;

  --primary: #60a5fa;
  --primary-soft: rgba(96, 165, 250, 0.12);
  --primary-dark: #3b82f6;

  --border: #1f2937;
  --border-soft: #111827;

  --shadow-soft: 0 0 0 1px rgba(148, 163, 184, 0.3);
  --shadow-card: 0 0 0 1px rgba(148, 163, 184, 0.45);
}

/* Base */
.teacher-admin-root,
body {
  margin: 0;
  padding: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    sans-serif;
  background: radial-gradient(circle at top, #e0edff 0, var(--bg) 55%);
  color: var(--text);
}

/* Reset some basics inside the app */
.teacher-admin-root * {
  box-sizing: border-box;
}

button {
  border: none;
  outline: none;
  font: inherit;
  background: transparent;
  cursor: pointer;
  transition: all 0.18s ease-out;
}

button:disabled {
  opacity: 0.5;
  cursor: default;
}

/* ================================
   THEME TOGGLE BUTTON
===================================*/
.theme-toggle-btn {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 6px 12px;
  border-radius: 999px;
  border: 1px solid var(--border);
  background: var(--card-soft);
  color: var(--text-muted);
  font-size: 12px;
  font-weight: 500;
}

.theme-toggle-btn:hover {
  background: var(--primary-soft);
  color: var(--primary-dark);
  border-color: var(--primary);
}

.theme-toggle-icon {
  display: flex;
  align-items: center;
}

/* ================================
   LOBBY UI
===================================*/

.lobby-container {
  min-height: 100vh;
  padding: 32px 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.lobby-content {
  width: 100%;
  max-width: 1140px;
  background: var(--card);
  border-radius: 24px;
  padding: 28px 28px 32px;
  box-shadow: var(--shadow-card);
  border: 1px solid var(--border-soft);
}

.lobby-header {
  border-bottom: 1px solid var(--border-soft);
  padding-bottom: 16px;
}

.lobby-header-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.lobby-header-text h1 {
  margin: 0;
  font-size: 28px;
  font-weight: 700;
  letter-spacing: -0.02em;
  color: var(--primary-dark);
}

.subtitle {
  margin-top: 4px;
  font-size: 14px;
  color: var(--text-muted);
}

/* Exam cards grid */
.exam-grid {
  margin-top: 24px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 18px;
}

.exam-card {
  background: radial-gradient(
      circle at top left,
      rgba(37, 99, 235, 0.06),
      transparent 60%
    ),
    var(--card-soft);
  border-radius: 18px;
  padding: 18px 18px 16px;
  border: 1px solid var(--border-soft);
  box-shadow: var(--shadow-soft);
  position: relative;
  overflow: hidden;
}

.exam-card::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 5px;
  height: 100%;
  background: linear-gradient(to bottom, var(--primary), var(--primary-dark));
  opacity: 0.7;
}

.exam-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 24px rgba(15, 23, 42, 0.2);
  border-color: var(--primary);
}

.exam-card h3 {
  margin: 0 0 8px 6px;
  font-size: 16px;
  font-weight: 700;
  color: var(--text);
}

.exam-meta {
  margin: 4px 0 14px;
  padding-left: 6px;
  font-size: 13px;
}

.meta-item {
  display: flex;
  align-items: center;
  gap: 6px;
  color: var(--text-muted);
  margin-bottom: 2px;
}

.meta-item i {
  font-style: normal;
}

.start-btn {
  display: block;
  width: 100%;
  padding: 8px 0;
  border-radius: 999px;
  background: linear-gradient(135deg, var(--primary), var(--primary-dark));
  color: #fff;
  font-size: 13px;
  font-weight: 600;
  text-align: center;
  letter-spacing: 0.02em;
}

.start-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 8px 18px rgba(37, 99, 235, 0.4);
}

/* admin panel button */
.admin-panel-btn {
  margin-top: 22px;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 8px 16px;
  border-radius: 999px;
  background: var(--bg-soft);
  color: var(--text-muted);
  border: 1px solid var(--border-soft);
  font-size: 13px;
  font-weight: 500;
}

.admin-panel-btn:hover {
  background: var(--primary-soft);
  color: var(--primary-dark);
  border-color: var(--primary);
}

/* ================================
   EXAM INTERFACE
===================================*/
.exam-interface {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Top bar */
.exam-topbar {
  height: 58px;
  background: var(--card);
  border-bottom: 1px solid var(--border-soft);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 18px 0 22px;
}

.exam-title {
  font-size: 16px;
  font-weight: 700;
  color: var(--primary-dark);
}

.exam-topbar-right {
  display: flex;
  align-items: center;
  gap: 16px;
}

/* Candidate info */
.candidate-info {
  display: flex;
  align-items: center;
  gap: 10px;
}

.candidate-details {
  text-align: right;
  font-size: 12px;
}

.candidate-details .name {
  font-weight: 600;
  color: var(--text);
}

.candidate-details .subject {
  color: var(--text-muted);
  font-size: 11px;
}

.candidate-avatar {
  width: 38px;
  height: 38px;
  border-radius: 999px;
  background: radial-gradient(circle at top, var(--primary), var(--primary-dark));
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-weight: 700;
  font-size: 14px;
}

/* Subject tabs + timer */
.subject-tabs-container {
  display: flex;
  align-items: flex-end;
  background: var(--card);
  border-bottom: 1px solid var(--border-soft);
  padding: 0 18px;
}

.subject-tab {
  padding: 10px 18px;
  border-radius: 8px 8px 0 0;
  margin-right: 4px;
  background: transparent;
  color: var(--text-muted);
  font-size: 13px;
  font-weight: 500;
}

.subject-tab:hover {
  background: var(--primary-soft);
  color: var(--primary-dark);
}

.subject-tab.active {
  background: var(--card-soft);
  color: var(--primary-dark);
  border: 1px solid var(--border-soft);
  border-bottom-color: var(--card-soft);
}

/* Timer */
.timer-container {
  margin-left: auto;
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 10px 0 10px 14px;
  font-size: 13px;
}

.timer-label {
  color: var(--text-muted);
  font-weight: 600;
}

.timer-display {
  min-width: 96px;
  text-align: center;
  padding: 4px 10px;
  border-radius: 999px;
  background: #111827;
  color: #fbbf24;
  font-weight: 700;
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas,
    "Liberation Mono", "Courier New", monospace;
}

/* Main layout */
.exam-main-content {
  display: flex;
  height: calc(100vh - 58px - 48px);
}

/* Question panel */
.question-panel {
  flex: 1;
  background: var(--card);
  border-right: 1px solid var(--border-soft);
  display: flex;
  flex-direction: column;
}

.question-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  border-bottom: 1px solid var(--border-soft);
}

.question-number {
  font-size: 15px;
  font-weight: 700;
  color: var(--primary-dark);
}

.question-actions {
  display: flex;
  gap: 6px;
}

.action-btn {
  width: 30px;
  height: 30px;
  border-radius: 8px;
  border: 1px solid var(--border);
  background: var(--card-soft);
  color: var(--text-muted);
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.action-btn:hover {
  background: var(--primary-soft);
  color: var(--primary-dark);
  border-color: var(--primary);
}

/* Question content */
.question-content {
  flex: 1;
  padding: 18px 22px 12px;
  overflow-y: auto;
}

.section-info {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
}

.section-badge {
  padding: 3px 9px;
  border-radius: 999px;
  background: var(--primary-soft);
  color: var(--primary-dark);
  font-size: 11px;
  font-weight: 600;
}

.question-type {
  font-size: 11px;
  font-weight: 700;
  color: var(--danger);
  text-transform: uppercase;
}

/* Passage */
.passage-box {
  margin: 8px 0 14px;
  padding: 10px 12px;
  border-radius: 10px;
  background: rgba(234, 179, 8, 0.08);
  border: 1px dashed rgba(234, 179, 8, 0.5);
  font-size: 13px;
}

.passage-box h4 {
  margin: 0 0 4px;
  font-size: 13px;
  font-weight: 700;
}

/* Question text */
.question-text {
  margin: 10px 0 14px;
  font-size: 15px;
  line-height: 1.6;
  font-weight: 500;
}

/* Options */
.options-grid {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.option-item {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  padding: 10px 12px;
  border-radius: 10px;
  border: 1px solid var(--border-soft);
  background: var(--card-soft);
  font-size: 14px;
}

.option-item:hover {
  border-color: var(--primary);
  background: var(--primary-soft);
}

.option-item.selected {
  border-color: var(--primary-dark);
  background: rgba(37, 99, 235, 0.12);
}

.option-radio {
  margin-top: 3px;
}

.option-radio input {
  width: 18px;
  height: 18px;
  accent-color: var(--primary-dark);
}

.option-label {
  flex: 1;
}

/* Numerical input */
.numerical-wrapper {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
}

.numerical-wrapper label {
  font-size: 14px;
  font-weight: 600;
}

.numerical-input {
  width: 180px;
  padding: 6px 10px;
  border-radius: 8px;
  border: 1px solid var(--border);
  background: var(--card-soft);
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas,
    "Liberation Mono", "Courier New", monospace;
}

.numerical-input:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 2px var(--primary-soft);
}

/* Matrix question */
.matrix-wrapper {
  margin-top: 10px;
}

.matrix-lists {
  display: flex;
  gap: 30px;
  font-size: 13px;
}

.matrix-list-item {
  margin-bottom: 2px;
}

.matrix-table {
  margin-top: 10px;
  border-collapse: collapse;
  font-size: 13px;
}

.matrix-table th,
.matrix-table td {
  border: 1px solid var(--border-soft);
  padding: 7px 8px;
  text-align: center;
}

.matrix-row-label {
  font-weight: 600;
  background: var(--card-soft);
}

.matrix-cell {
  cursor: pointer;
  background: var(--card);
}

.matrix-cell:hover {
  background: var(--primary-soft);
}

.matrix-cell.selected {
  background: var(--primary-dark);
  color: #fff;
}

.matrix-checkbox {
  width: 14px;
  height: 14px;
  border-radius: 4px;
  border: 2px solid currentColor;
  margin: 0 auto;
}

/* Bottom controls */
.question-controls {
  border-top: 1px solid var(--border-soft);
  padding: 10px 18px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--card);
}

.control-left {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.control-btn {
  padding: 7px 13px;
  border-radius: 999px;
  font-size: 12px;
  font-weight: 600;
  border: 1px solid var(--border-soft);
  background: var(--card-soft);
  color: var(--text);
}

.btn-save {
  background: var(--success);
  border-color: transparent;
  color: #fff;
}

.btn-clear {
  background: var(--danger);
  border-color: transparent;
  color: #fff;
}

.btn-review {
  background: var(--warning);
  border-color: transparent;
  color: #fff;
}

.btn-mark {
  background: var(--info);
  border-color: transparent;
  color: #fff;
}

.btn-next {
  background: var(--primary-dark);
  border-color: transparent;
  color: #fff;
}

/* Palette panel */
.palette-panel {
  width: 270px;
  background: var(--card);
  display: flex;
  flex-direction: column;
  box-shadow: -4px 0 14px rgba(15, 23, 42, 0.08);
}

.palette-header {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 14px 16px;
  border-bottom: 1px solid var(--border-soft);
}

.palette-avatar {
  width: 40px;
  height: 40px;
  border-radius: 999px;
  background: radial-gradient(circle at top, var(--success), #22c55e);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #f9fafb;
  font-weight: 700;
}

.palette-student-name {
  font-weight: 600;
  font-size: 14px;
}

/* Legend */
.legend-container {
  padding: 10px 14px;
  border-bottom: 1px solid var(--border-soft);
}

.legend-grid {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 6px 10px;
  font-size: 11px;
  color: var(--text-muted);
}

.legend-item {
  display: flex;
  align-items: center;
  gap: 6px;
}

.legend-box {
  width: 22px;
  height: 22px;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 11px;
  font-weight: 600;
}

/* Status colors */
.answered {
  background: var(--success);
  color: #fff;
}

.not-answered {
  background: var(--danger);
  color: #fff;
}

.not-visited {
  background: var(--not-visited);
  color: #fff;
}

.marked {
  background: var(--warning);
  color: #fff;
}

.marked-answered {
  background: var(--warning);
  color: #fff;
  outline: 2px solid var(--success);
}

/* Palette buttons */
.palette-content {
  flex: 1;
  padding: 12px 14px;
  overflow-y: auto;
}

.subject-section {
  margin-bottom: 6px;
}

.subject-title {
  padding: 6px 10px;
  border-radius: 8px;
  background: var(--bg-soft);
  color: var(--primary-dark);
  font-size: 12px;
  font-weight: 600;
  margin-bottom: 8px;
}

.question-palette-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 6px;
}

.palette-btn {
  width: 40px;
  height: 32px;
  border-radius: 8px;
  border: 1px solid var(--border-soft);
  font-size: 13px;
  font-weight: 600;
  background: var(--card-soft);
  color: var(--text);
}

.palette-btn.current {
  border: 2px solid var(--primary-dark);
  box-shadow: 0 0 0 2px var(--primary-soft);
}

/* Submit */
.palette-footer {
  padding: 10px 14px 12px;
  border-top: 1px solid var(--border-soft);
}

.btn-submit-exam {
  width: 100%;
  padding: 9px 0;
  border-radius: 999px;
  background: linear-gradient(135deg, var(--primary-dark), #1e40af);
  color: #fff;
  font-size: 13px;
  font-weight: 700;
}

/* ================================
   ADMIN BUILDER
===================================*/

.admin-builder {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: radial-gradient(circle at top, #dbeafe 0, var(--bg) 40%);
}

/* Header */
.builder-header {
  background: var(--card);
  border-bottom: 1px solid var(--border-soft);
  padding: 14px 22px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: var(--shadow-soft);
}

.builder-title {
  display: flex;
  align-items: center;
  gap: 12px;
}

.back-btn {
  width: 38px;
  height: 38px;
  border-radius: 999px;
  background: #f97316;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}

.back-btn:hover {
  transform: translateX(-2px);
  box-shadow: 0 8px 18px rgba(249, 115, 22, 0.5);
}

.builder-title h2 {
  margin: 0;
  font-size: 20px;
  font-weight: 700;
}

.builder-right {
  display: flex;
  align-items: center;
  gap: 14px;
}

.builder-actions {
  display: flex;
  gap: 8px;
}

.btn-discard {
  padding: 7px 14px;
  border-radius: 999px;
  border: 1px solid var(--border-soft);
  background: var(--card-soft);
  color: var(--text-muted);
  font-size: 13px;
  font-weight: 500;
}

.btn-discard:hover {
  border-color: var(--danger);
  color: var(--danger);
}

.btn-save-question {
  padding: 7px 16px;
  border-radius: 999px;
  background: linear-gradient(135deg, var(--primary), var(--primary-dark));
  color: #fff;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  font-size: 13px;
  font-weight: 600;
}

/* Content layout */
.builder-content {
  flex: 1;
  display: flex;
  min-height: 0;
}

/* Sidebar */
.builder-sidebar {
  width: 270px;
  padding: 18px 18px 20px;
  background: var(--card);
  border-right: 1px solid var(--border-soft);
  overflow-y: auto;
}

.sidebar-section-title {
  font-size: 11px;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  color: var(--text-muted);
  font-weight: 700;
  margin-bottom: 10px;
}

.type-btn {
  width: 100%;
  text-align: left;
  padding: 9px 11px;
  border-radius: 10px;
  border: 1px solid var(--border-soft);
  background: var(--card-soft);
  font-size: 13px;
  font-weight: 500;
  color: var(--text);
  margin-bottom: 6px;
}

.type-btn:hover {
  border-color: var(--primary);
  background: var(--primary-soft);
}

.type-btn.active {
  border-color: var(--primary-dark);
  background: radial-gradient(
      circle at top left,
      rgba(37, 99, 235, 0.2),
      transparent 70%
    ),
    var(--card-soft);
  color: var(--primary-dark);
}

/* Main form */
.builder-main {
  flex: 1;
  padding: 18px 22px 26px;
  overflow-y: auto;
}

.form-section {
  background: var(--card);
  border-radius: 16px;
  border: 1px solid var(--border-soft);
  padding: 16px 18px 18px;
  margin-bottom: 16px;
  box-shadow: var(--shadow-soft);
}

.form-section-title {
  font-size: 14px;
  font-weight: 700;
  margin-bottom: 10px;
  color: var(--primary-dark);
}

.form-textarea {
  width: 100%;
  min-height: 120px;
  border-radius: 12px;
  border: 1px solid var(--border-soft);
  padding: 10px 12px;
  background: var(--card-soft);
  font-size: 14px;
  resize: vertical;
}

.form-textarea:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 2px var(--primary-soft);
}

/* Options form */
.option-form-item {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
}

.option-letter {
  width: 30px;
  height: 30px;
  border-radius: 999px;
  background: var(--primary-soft);
  color: var(--primary-dark);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  flex-shrink: 0;
}

.option-input {
  flex: 1;
  border-radius: 999px;
  border: 1px solid var(--border-soft);
  background: var(--card-soft);
  padding: 7px 11px;
  font-size: 13px;
}

.option-input:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 2px var(--primary-soft);
}

.option-checkbox {
  width: 20px;
  height: 20px;
  accent-color: var(--primary-dark);
}

.btn-add-option {
  margin-top: 2px;
  font-size: 13px;
  color: var(--primary-dark);
}

/* Matrix builder */
.matrix-builder {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(230px, 1fr));
  gap: 18px;
  margin-top: 4px;
}

.matrix-column h4 {
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  color: var(--text-muted);
  margin-bottom: 8px;
}

.matrix-row {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 8px;
}

.matrix-label {
  width: 30px;
  height: 30px;
  border-radius: 999px;
  background: var(--bg-soft);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  color: var(--text);
}

.matrix-text {
  flex: 1;
  border-radius: 999px;
  border: 1px solid var(--border-soft);
  padding: 7px 11px;
  background: var(--card-soft);
  font-size: 13px;
}

.matrix-text:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 2px var(--primary-soft);
}

.btn-add-row,
.btn-add-col {
  font-size: 12px;
  font-weight: 600;
  color: var(--primary-dark);
}

/* Numerical preview */
.numerical-preview {
  border-radius: 12px;
  border: 1px dashed var(--border-soft);
  padding: 12px 14px;
  background: var(--card-soft);
}

.preview-label {
  font-size: 13px;
  color: var(--text-muted);
  margin-bottom: 8px;
}

.preview-input {
  border-radius: 999px;
  border: 1px solid var(--border-soft);
  padding: 6px 12px;
  width: 200px;
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas,
    "Liberation Mono", "Courier New", monospace;
}

.validation-badge {
  display: inline-block;
  margin-left: 10px;
  border-radius: 999px;
  padding: 4px 10px;
  background: rgba(245, 158, 11, 0.1);
  color: #b45309;
  font-size: 11px;
  font-weight: 600;
}

/* ================================
   RESULT PAGE
===================================*/
.result-container {
  min-height: 100vh;
  padding: 28px 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.result-card {
  width: 100%;
  max-width: 720px;
  background: var(--card);
  border-radius: 22px;
  border: 1px solid var(--border-soft);
  box-shadow: var(--shadow-card);
  overflow: hidden;
}

.result-header {
  padding: 18px 22px 14px;
  background: linear-gradient(135deg, var(--primary-dark), #172554);
  color: #e5e7eb;
}

.result-header h2 {
  margin: 0 0 2px;
  font-size: 20px;
}

.result-subtitle {
  font-size: 13px;
  opacity: 0.9;
}

.result-content {
  padding: 20px 22px 24px;
}

/* Score summary */
.score-summary {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 14px;
  margin-bottom: 18px;
}

.score-card {
  border-radius: 16px;
  padding: 16px 14px;
  text-align: center;
}

.score-card.primary {
  background: linear-gradient(135deg, var(--primary-dark), var(--primary));
  color: #fff;
}

.score-card.secondary {
  background: var(--card-soft);
  color: var(--text);
  border: 1px solid var(--border-soft);
}

.score-value {
  font-size: 30px;
  font-weight: 700;
}

.score-label {
  margin-top: 4px;
  font-size: 11px;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

/* Stats list */
.result-stats {
  margin-top: 6px;
}

.stat-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 0;
  border-bottom: 1px solid var(--border-soft);
}

.stat-label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
}

.stat-icon {
  width: 22px;
  height: 22px;
  border-radius: 999px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
}

.stat-icon.correct {
  background: var(--success);
}

.stat-icon.incorrect {
  background: var(--danger);
}

.stat-value {
  font-weight: 700;
  font-size: 16px;
}

.stat-value.correct {
  color: var(--success);
}

.stat-value.incorrect {
  color: var(--danger);
}

/* Return button */
.btn-return {
  margin-top: 18px;
  width: 100%;
  padding: 10px 0;
  border-radius: 999px;
  background: linear-gradient(135deg, var(--primary-dark), #1e40af);
  color: #fff;
  font-size: 14px;
  font-weight: 700;
}

/* ================================
   ANIMATIONS & RESPONSIVE
===================================*/

.fade-in {
  animation: fadeIn 0.40s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive tweaks */
@media (max-width: 1024px) {
  .exam-main-content {
    flex-direction: column;
  }

  .palette-panel {
    width: 100%;
    order: -1;
    flex-direction: row;
    overflow-x: auto;
  }

  .palette-content {
    flex: 1;
  }

  .builder-content {
    flex-direction: column;
  }

  .builder-sidebar {
    width: 100%;
    border-right: none;
    border-bottom: 1px solid var(--border-soft);
  }
}

@media (max-width: 768px) {
  .lobby-content {
    padding: 20px 18px 24px;
    border-radius: 18px;
  }

  .lobby-header-top {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }

  .builder-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  .score-summary {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 520px) {
  .question-controls {
    flex-direction: column;
    align-items: stretch;
    gap: 6px;
  }

  .control-left {
    justify-content: center;
  }

  .subject-tabs-container {
    overflow-x: auto;
  }

  .timer-container {
    padding-right: 8px;
  }
}
