.blog-page {
  width: 90%;
  max-width: 1250px;
  margin: 0 auto;
  padding: 40px 0;
  font-family: sans-serif;
}

/* HERO */
.blog-hero {
  text-align: center;
  padding: 20px 0 40px;
}

.blog-hero h1 {
  font-size: 42px;
  font-weight: 800;
}

.blog-hero p {
  font-size: 17px;
  margin-top: 10px;
  color: #555;
}

/* BLOG GRID */
.blog-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 35px;
  margin-top: 30px;
}

/* CARD */
.blog-card {
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 6px 28px rgba(0,0,0,0.08);
  overflow: hidden;
  transition: 0.3s ease;
}

.blog-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 14px 36px rgba(0,0,0,0.12);
}

.blog-img {
  width: 100%;
  height: 190px;
  object-fit: cover;
}

.blog-content {
  padding: 18px 22px 25px;
}

/* TAG + DATE */
.blog-meta {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  font-size: 14px;
}

.tag {
  background: #ff7a00;
  padding: 4px 14px;
  border-radius: 20px;
  color: white;
}

.date {
  color: #777;
}

/* TITLE */
.blog-title {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 10px;
}

/* DESCRIPTION */
.blog-desc {
  color: #555;
  font-size: 15px;
  margin-bottom: 16px;
}

/* BUTTON */
.read-btn {
  padding: 10px 20px;
  background: #ff7a00;
  color: #fff;
  border: none;
  border-radius: 30px;
  font-weight: 600;
  cursor: pointer;
}

.read-btn:hover {
  background: #e86900;
}

/* RESPONSIVE */
@media (max-width: 900px) {
  .blog-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 600px) {
  .blog-grid {
    grid-template-columns: 1fr;
  }
}
